.hiui-popover {
  position: absolute;
  z-index: 199;
  background-color: #fff;
  min-width: 180px;
  border: 1px solid #ccc;
  border-radius: 3px;
}
.hiui-popover-use_shadow {
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.12), 0 0 6px 0 rgba(0,0,0,0.04);
}
.hiui-popover-left::before {
  content: '';
  position: absolute;
  height: 0;
  width: 0;
  border-color: transparent;
  border-style: solid;
  border-width: 6px;
  border-left-color: inherit;
  left: 100%;
  top: 50%;
  transform: translateY(-50%);
}
.hiui-popover-left::after {
  content: '';
  position: absolute;
  height: 0;
  width: 0;
  border-color: transparent;
