.hiui-popover-slide-down-enter,
.hiui-popover-slide-down-leave-to {
  transform: translateY(-30px);
  opacity: 0;
}
.hiui-popover-slide-down-enter-active,
.hiui-popover-slide-down-leave-active {
  transition: transform 0.3s ease, opacity 0.3s ease;
}
.hiui-popover-slide-up-enter,
