
:export {
  gap_xsm: 14px;
  gap_sm: 20px;
  gap_md: 30px;
  gap_lg: 44px;
  gap_xlg: 62px;
}
:export {
  size_o_sm: 28px;
  size_o_md: 40px;
  size_o_lg: 56px;
}
:export {
  size_sm: 24px;
  size_md: 34px;
