.hiui-toast {
  position: fixed;
  top: 50px;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 3px;
  color: #fff;
  z-index: 99999;
  background-color: #fff;
  letter-spacing: -0.2em;
  border-radius: 3px;
  overflow: hidden;
}
.hiui-toast-default {
  border: 1px solid #666;
}
.hiui-toast-default .hiui-toast-content {
  background-color: #666;
}
.hiui-toast-default .hiui-icon {
  color: #666;
}
.hiui-toast-primary {
  border: 1px solid #00a0e9;
}
.hiui-toast-primary .hiui-toast-content {
  background-color: #00a0e9;
}
.hiui-toast-primary .hiui-icon {
  color: #00a0e9;
}
.hiui-toast-error {
  border: 1px solid #f02f45;
}
.hiui-toast-error .hiui-toast-content {
  background-color: #f02f45;
}
.hiui-toast-error .hiui-icon {
  color: #f02f45;
}
.hiui-toast-warning {
  border: 1px solid #fac450;
}
.hiui-toast-warning .hiui-toast-content {
  color: #333;
  background-color: #fac450;
}
.hiui-toast-warning .hiui-icon {
  color: #fac450;
}
.hiui-toast-success {
  border: 1px solid #4fae9b;
}
.hiui-toast-success .hiui-toast-content {
  background-color: #4fae9b;
}
.hiui-toast-success .hiui-icon {
  color: #4fae9b;
}
.hiui-toast .hiui-icon {
  display: inline-block;
  width: 30px;
  text-align: center;
  height: 100%;
}
.hiui-toast-content {
  display: inline-block;
  padding: 8px 15px;
  background-color: #666;
  letter-spacing: 0;
}
.hiui_fade-enter-active,
.hiui_fade-leave-active {
  transition: all 0.3s;
}
.hiui_fade-enter,
.hiui_fade-leave-to {
  opacity: 0;
  transform: translate(-50%, -50%) scale(0.9);
}


